  padding: 0px 32px 14px 120px;
  font-family: "Source Sans Pro";
  font-weight: 700;
  font-size: 23px;
  line-height: 36px;
}
.structure__status {
  margin: 0;
  padding: 0px 32px 0px 75px;
  font-family: "Source Sans Pro";
  font-weight: 400;
  font-size: 23px;
  line-height: 36px;
}
.structure__img-bootom {
  height: 400px;
  background-image: url("../img/president.png");
  object-fit: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.banner__container {
  display: grid;
  grid-template-columns: 296px 1fr;
}
.banner__right {
  background-color: var(--dark-color);
  color: var(--light-color);
  display: flex;
}
.banner__img {
  background-image: url("../img/president.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  margin: 70px 70px 0px 70px;
  height: 200px;
  width: 200px;
  border-radius: 50%;
  overflow: hidden;
}
.banner__descr {
  max-width: 900px;
  padding: 70px 80px 70px 0px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}
.banner__svg {
  color: #fff;
}
.banner__name {
  text-align: center;
  font-weight: 700;
  font-size: 16px;
  padding: 0px 70px 0px 70px;
}
.banner__name span {
  display: block;
  font-weight: 400;
}

.history {
  color: var(--very-dark-gray);
}
.history__container {
  display: grid;
