  font-weight: 400;
  font-size: 23px;
  line-height: 36px;
  margin-bottom: 103px;
}
.history__descr:nth-child(1) {
  max-width: 270px;
  margin-bottom: 75px;
}
.history__descr:nth-child(6) {
  margin-bottom: 65px;
}
.history__yers {
  text-align: right;
  margin: 0;
  font-family: "Merriweather";
  font-weight: 700;
  font-size: 42px;
  line-height: 44px;
  margin-bottom: 95px;
}
.history__yers:nth-child(1) {
  margin-bottom: 100px;
}
.history__yers:nth-child(2) {
