  left: 123px;
}
.fclub__btn {
  height: 32px;
  border-radius: 4px;
  padding: 4px 16px;
  background: #0082FF;
  color: var(--light-color);
  border-radius: 4px;
  position: relative;
  top: 276px;
  left: 38%;
  cursor: pointer;
}

.about__container {
  display: grid;
  grid-template-columns: 296px 1fr;
}
.about__right {
  background-color: #FCFCFC;
}
.about__title {
  margin: 0;
  padding: 53px 32px 109px 32px;
  font-weight: 700;
