.fedback__container {
  display: grid;
  grid-template-columns: 296px 1fr;
}
.fedback__slider {
  width: 1093px;
}
.fedback__right {
  background-color: #FCFCFC;
}
.fedback__title {
  margin: 0;
  padding: 10px 32px 49px 32px;
  font-weight: 700;
  font-size: 36px;
  line-height: 44px;
  color: var(--dark-gray);
}
.fedback__pogination {
  display: none;
}
.fedback .swiper {
  padding-bottom: 43px;
}
.fedback .swiper-wrapper {
  padding: 0px 20px;
}
.fedback .swiper-button-prev.fedback__btn-prev {
  top: 60px;
  left: -160px;
  height: 81px;
  width: 29px;
  background-image: url("../img/svg/arr_prev.svg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.fedback .swiper-button-next.fedback__btn-next {
