.footer__container {
  display: grid;
  grid-template-columns: 296px 1fr;
}
.footer__right {
  background-color: #101010;
  color: var(--light-color);
}
.footer__nav {
  padding: 40px 24px;
  display: flex;
  align-items: start;
  justify-content: space-around;
}
.footer__list {
  display: block;
  padding-bottom: 20px;
}
.footer__form {
  display: flex;
  flex-direction: column;
}
.footer__item-bold {
  padding: 0px 0px 32px 0px;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
}
.footer__item {
  padding: 0px 0px 16px 0px;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: #989898;
}
.footer__item span {
  color: #0082FF;
}
.footer__item-about {
  border-radius: 4px;
  max-width: 352px;
  background: #2D2D2D;
  padding: 4px 22px;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  height: 32px;
}
.footer__item-about span {
  color: #0082FF;
  padding-right: 8px;
}
.footer__input {
  border-radius: 4px;
  border: 1px solid #2D2D2D;
  padding: 12px 16px;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: #787878;
  height: 48px;
  max-width: 352px;
  background-color: transparent;
  margin-bottom: 16px;
}
.footer__checkbox-container {
  max-width: 352px;
  border-radius: 4px;
  padding: 24px 16px;
  display: flex;
  flex-direction: column;
  background: #232323;
}
.footer__checkbox {
  margin-left: 100px;
}
.footer__descr-box {
  background: #181716;
  padding: 40px 24px;
}
.footer__descr {
  margin: 0;
  padding: 0px 0px 20px 0px;
  max-width: 627px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #989898;
}
.footer__descr span {
  color: #0082FF;
}
.footer__descr-bottom {
  margin: 0;
  max-width: 627px;
  font-weight: 400;
  font-size: 13px;
  line-height: 18px;
  color: #989898;
}
.footer .custom-checkbox {
  display: flex;
  justify-content: space-between;
}
.footer .custom-checkbox__content {
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: #989898;
}
.footer .custom-checkbox:not(:last-child) {
  padding-bottom: 24px;
}
.footer .img__title {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-family: "Merriweather";
  font-weight: 700;
  font-size: 22px;
  line-height: 28px;
  color: var(--light-color);
}